import { Component, h, Element, State, Host } from '@stencil/core';

/**
 * @category Navigation
 * @description A sidebar trigger component for the sidebar.
 */

@Component({
    tag: 'at-sidebar-trigger',
    scoped: true,
    styleUrls: ['./at-sidebar-trigger.scss'],
})
export class AtSidebarTriggerComponent {
    @Element() el: HTMLElement;
    @State() isOpen: boolean = false;

    private provider: HTMLAtSidebarElement;

    private async updateIsOpen() {
        if (this.provider && typeof this.provider.getIsOpen === 'function') {
            this.isOpen = await this.provider.getIsOpen();
        }
    }

    private async toggleSidebar(): Promise<void> {
        if (
            this.provider &&
            typeof this.provider.toggleSidebar === 'function'
        ) {
            await this.provider.toggleSidebar();
            await this.updateIsOpen();
        }
    }

    private handleKeyDown = (event: KeyboardEvent): void => {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            this.el.click();
        }
    };

    async componentDidLoad() {
        this.provider = this.el.closest('at-sidebar') as HTMLAtSidebarElement;
        await this.updateIsOpen();

        if (this.provider) {
            this.provider.addEventListener(
                'atuiSidebarChange',
                this.handleSidebarChange,
            );
        }
    }

    disconnectedCallback() {
        if (this.provider) {
            this.provider.removeEventListener(
                'atuiSidebarChange',
                this.handleSidebarChange,
            );
        }
    }

    private handleSidebarChange = (event: CustomEvent<boolean>) => {
        this.isOpen = event.detail;
    };

    render() {
        return (
            <Host
                role="button"
                data-name="sidebar-trigger"
                tabIndex={0}
                onKeyDown={this.handleKeyDown}
                onClick={() => this.toggleSidebar()}
            >
                {this.isOpen !== undefined && (
                    <i class="material-icons">
                        {this.isOpen ? 'menu_open' : 'menu'}
                    </i>
                )}
                <div
                    class="focus-indicator"
                    data-name="focus-indicator"
                    role="presentation"
                ></div>
            </Host>
        );
    }
}
