@use '../../../styles/shared/icon' as *;

:host {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: var(--token-border-radius-md);
    cursor: pointer;
    padding: 6px;
    font-weight: var(--token-font-weight-med);
    color: var(--token-sidebar-foreground);
    background-color: var(--token-sidebar-background);
    transition:
        background-color var(--token-transition-time-fast),
        color var(--token-transition-time-fast),
        box-shadow var(--token-transition-time-fast);

    i.icon {
        font-family: 'Material Icons';
        font-style: normal;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 1;
        font-size: var(--token-font-size-icon-lg) !important;
        z-index: 10;
    }

    .focus-indicator {
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: var(--token-sidebar-foreground);
        opacity: 0;
        transition: background-color var(--token-transition-time-fast);
    }

    &:hover .focus-indicator {
        opacity: 0.1;
    }

    &:focus-visible {
        outline: none;
        box-shadow: 0 0 0 2px
            color-mix(
                in srgb,
                var(--token-state-active-foreground) 40%,
                transparent
            );
    }
}
